import { IconBespoke } from '../Icons'
import FooterBlurb from '../FooterBlurb'
export default {
  name: 'ProductBespoke',
  render() {
    return (
      <div class="pop-page" id="page-product-bespoke">
        <IconBespoke invert />
        <h1>bespoke</h1>
        <p>
          If your client has particular needs that cant be met by our
          market-aligned products, well work with you to tailor bespoke
          solutions in a transparent and consultative way.
        </p>
        <p>
          At blend, we service the market needs of today while designing
          products for tomorrow. If you have a product concept or idea that
          answers a specific unmet market need and youd like to validate it,
          get in touch so we can bring it to life together.
        </p>
        <p>
          Its our priority to develop both corporate and consumer solutions
          that complement one another by addressing both markets, to avoid
          overlapping and confusion. If its not designed to simplify and
          improve the user experience, its not blend.
        </p>

        <div class="pop-foot">
          <FooterBlurb />
        </div>
      </div>
    )
  }
}



